import JsonViewer from '@/components/JsonViewer'
import StatusActive from '@/components/StatusActive'
import VueNumeric from 'vue-numeric'
export default {
    name: 'employee-view',
    data() {
        return {
            employee: {
                id: '',
                employeeId: '',
                name: '',
                address: '',
                gender: '',
                nik: '',
                npwp: '',
                taxStatus: '',
                phone: '',
                email: '',
                accountNameP: '',
                accountNumberP: '',
                bankNameP: '',
                accountNameL: '',
                accountNumberL: '',
                bankNameL: '',
                isActive: false,
                cvUrl: '',
                role: '',
                loanLimit: 0,
                salary: 0,
                joinDate: ''
            },
            error: ''
        }
    },
    components: {
        JsonViewer,
        StatusActive,
        VueNumeric
    },
    computed: {
        getEmployeeGender() {
            return this.employee.gender == 'm' ? 'Male' : 'Female'
        }
    },
    methods: {
        downloadCv() {
            this.$http({
                method: 'get',
                url: `/employee/managed-employee/${this.employee.id}/download-cv`,
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                this.cv = response.data
                console.log('CV Success', response.data)
                const linkSource = `data:application/pdf;base64,${this.cv.info}`
                const downloadLink = document.createElement('a')
                const fileName = `cv_${this.employee.id}.pdf`
                downloadLink.href = linkSource
                downloadLink.download = fileName
                downloadLink.click()
            })
            .catch(error => {
                this.errorMessage = error.response.data
            })
        },
        getEmployee(_id) {
            this.$http({
                method: 'post',
                url: '/employee/find-by-id',
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                },
                data: {
                    id_employee: _id
                }
            })
            .then(response => {
                let rEmployee = response.data
                this.employee = {
                    id: _id,
                    employeeId: rEmployee.employee_id,
                    name: rEmployee.name,
                    address: rEmployee.address,
                    gender: rEmployee.gender,
                    nik: rEmployee.nik,
                    npwp: rEmployee.npwp,
                    taxStatus: rEmployee.tax_status,
                    phone: rEmployee.cellphone,
                    email: rEmployee.email,
                    accountNameP: rEmployee.account_name_p,
                    accountNumberP: rEmployee.account_number_p,
                    bankNameP: rEmployee.bank_name_p,
                    accountNameL: rEmployee.account_name_l,
                    accountNumberL: rEmployee.account_number_l,
                    bankNameL: rEmployee.bank_name_l,
                    isActive: rEmployee.is_active,
                    cvUrl: rEmployee.doc_cv,
                    role: rEmployee.role,
                    loanLimit: rEmployee.loan_limit,
                    salary: rEmployee.salary,
                    joinDate: rEmployee.join_date
                }
            })
            .catch(error => {
                this.error = error.response.data
            })
        }
    },
    created() {
        this.employee.id = this.$route.params.id
        this.getEmployee(this.employee.id)
    }
}